<template>
    <img :status="status" :src="url" class="atom-lazyload" :lazy="status"/>
</template>
<script>
// vue-lazyload, 
// https://github.com/hilongjw/vue-lazyload
// IntersectionObserver, , caniuse>=5
// https://developer.mozilla.org/zh-CN/docs/Web/API/Intersection_Observer_API
// indexeddbid, .
// , 
import { getWidth, getHeight, getScrollParent, getTime, getIsInView } from '@/utils/dom';
import throttle from 'lodash/throttle';
export default {
    name: 'LazyLoad',

    props: {
        src: {
            type: String
        },

        placeholderBackgroundColor: {
            type: String,
            default: '#eee'
        },

        placeholder: {
            type: String,
            default() {
                return `data:image/svg+xml;utf8,<svg width="1" height="1" version="1.1" xmlns="http://www.w3.org/2000/svg"><rect width="1" height="1" fill="${this.placeholderBackgroundColor}"/></svg>`;
            }
        },

        throttleTime: {
            type: Number,
            default: 200
        },

        attempt: {
            type: Number,
            default: 3
        },

        errorImg: {
            type: String,
            default: ''
        },

        preLoadRate: {
            type: Number,
            default: 1.3
        },

        events: {
            type: Array,
            default() {
                return ['scroll', 'wheel', 'mousewheel', 'resize', 'animationend', 'transitionend', 'webkitAnimationend', 'webkitTransitionend', 'touchmove'];
            }
        },

        refresh: {
            type: Number
            // default: getTime()
            // 2v-show
            // refresh,
            // v-if, mounted.
        },

        isRefresh: {
            type: Boolean,
            default: false
        }
    },

    data() {
        return {
            status: 'ready',
            url: this.placeholder,
            costTime: 0,
            attemptCount: 0,
            scrollParentNode: null
        };
    },

    created() {
        // 
        // this.url = this.placeholder;
    },

    mounted() {
        // , 
        this.scrollParentNode = getScrollParent(this.$el);
        // 
        this.events.forEach(eventName => {
            this.scrollParentNode.addEventListener(eventName, this.loadInViewImg);
        });
        this.loadInViewImg();
    },

    methods: {
        /**
         * , 
         * 
         */
        loadInViewImg() {
            // throttle.cancel?
            throttle(() => {
                if ('loaded' === this.status) {
                    this.events.forEach(eventName => {
                        this.scrollParentNode.removeEventListener(eventName, this.loadInViewImg);
                    });
                } else if ('ready' === this.status) {
                    if (getIsInView(this.$el, this.preLoadRate)) {
                        this.loadImg();
                    }
                }
            }, 200)();
        },

        loadImg() {
            const startTime = getTime();
            var img = new Image();
            img.src = this.src;

            // if (img.complete) {
            //     this.status = 'loaded';
            //     this.url = this.src;
            //     this.costTime = getTime() - startTime;
            //     this.$emit('loaded', {
            //         width: img.naturalWidth,
            //         height: img.naturalHeight,
            //         url: this.url,
            //         costTime: this.costTime
            //     });
            //     img = null;
            //     return;
            // }

            if (this.attempt > this.attemptCount) {
                this.attemptCount++;
                // placeholder, img.complete,
                // , , 
                this.status = 'loading';
                // 
                img.onload = e => {
                    this.status = 'loaded';
                    this.url = this.src;
                    this.costTime = getTime() - startTime;
                    this.$emit('loaded', {
                        width: img.naturalWidth,
                        height: img.naturalHeight,
                        url: this.url,
                        costTime: this.costTime
                    });
                    img = null;
                };

                // 
                img.onerror = e => {
                    this.status = 'attempt';
                    this.$emit('attempt', {
                        url: this.url,
                        costTime: this.costTime
                    });
                    this.loadImg();
                };
            } else {
                if ('' == this.errorImg) {
                    this.url = this.errorImg;
                }
                this.status = 'fail';
                this.$emit('fail', {
                    url: this.url,
                    costTime: this.costTime
                });
                img = null;
            }
        }
    },

    watch: {
        refresh() {
            this.loadInViewImg();
        },

        isRefresh() {
            this.loadInViewImg();
        }
    },

    beforeDestroy() {
        this.events.forEach(eventName => {
            this.scrollParentNode.removeEventListener(eventName, this.loadInViewImg);
        });
    }
};
</script>
<style scoped lang="scss">
@import '../../scss/variables.scss';
$size: 0.6rem;
.atom-lazyload {
    max-width: 100%;
    display: block;
    background-position: center center;
    // &[lazy='loading'] {
    //     animation: ripple 1000ms infinite;
    // }
    &[lazy='loaded'] {
        animation: loaded 1000ms;
    }

    // &[lazy='attempt'] {

    // }

    // &[lazy='fail'] {
    //     animation: fadeIn 1s;
    // }
    @keyframes loaded {
        0% {
            transform: scale(1.1);
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }
}
</style>



